<template>
    <div class="navigation-box">
        <ul class="navigation">
            <li v-for="(item, index) in navList"
                class="navigation-list-item"
                :key="index"
                @click="toggleNavClick(item, index)">
                <span>{{item.name}}</span>
                <img src="../../assets/publish/radio.png" v-if="activeInd == index">
            </li>
        </ul>
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
export default {
    data () {
        return {
            activeInd: 0
        };
    },
    computed: {
        ...mapState({
            tradeType: state => state.home.tradeType,
            navList: state => state.home.navList
        })
    },
    methods: {
        ...mapActions({
            getTypeList: 'getTypeList'
        }),
        ...mapMutations({
            setTradeType: 'setTradeType',
            setNavList: 'setNavList'
        }),
        // 点击路由跳转
        toggleNavClick (item, index) {
            this.activeInd = index;
            this.setTradeType({
                ind: index,
                type: item.code
            });
            this.setNavList([]);
            this.$router.push({
                name: 'home'
            });
        }
        // 获取类型数据
    },
    created () {
        this.$router.setTitle(this.$t('title.tradeType'));
        let params = {
            type: 1
        };
        this.getTypeList(params);
    },
    mounted () {
        // 判断是否高亮筛选图标
        if (this.tradeType) {
            this.activeInd = this.tradeType.ind;
        }
    }
};
</script>

